import Vue from 'vue'
import VueResource from 'vue-resource'

Vue.use(VueResource)

class Test {
  Api = {
    'getPosts': '/static/posts.json',
    'login': '/login/',
    'userInfo': '/user/'
  }
  getData (url) {
    return Vue.http.get(url).then(response => {
      return response.data
    }, response => {
      console.log('get posts fail')
    })
  }
  postData (url, data) {
    return Vue.http.post(url, data).then(response => {
      return response
    }, response => {
      console.log('post fail')
      return response
    })
  }
  jsonpData (url, data) {
    return Vue.http.jsonp(url, data).then(response => {
      return response.data
    }, response => console.log('jsonp data fail'))
  }
}

export default new Test()



