<template>
  <mu-appbar>
    <mu-flat-button label="Blog" class="demo-flat-button" icon="home" slot="left" to="/" primary/>
    <mu-flat-button v-bind:label="name" slot="right"/>
    <mu-icon-menu icon="more_vert" slot="right">
      <mu-menu-item v-for="item in items" v-bind:title="item" :key="item.id" href="#/login/"/>
    </mu-icon-menu>
  </mu-appbar>
</template>

<script>
  import test from '../services/api'
  export default {
    data: function () {
      return {
        title: 'blog',
        items: ['a', 'b', 'c'],
        name: ''
      }
    },
    mounted: function () {
//      this.articles = test.getData('/static/test.json')
      test.getData('/static/test.json').then(data => {
        this.items = data.items
        this.name = data.name
      })
    },
    methods: {
      'Home': function () {
        alert('home')
      }
    }
  }
</script>



