<template>
  <mu-card class="login">
    <div>
      <mu-flexbox orient="vertical" align="center" style="text-align: center;">
        <mu-flexbox-item>
          <mu-text-field
            label="邮箱"
            type="email"
            icon="account_circle"
            v-model="email"
            v-if="error === ''"
            labelFloat/>
          <mu-text-field
            label="邮箱"
            type="email"
            icon="account_circle"
            v-model="email"
            v-bind:errorText="error"
            v-if="error !== ''"
            labelFloat/>
        </mu-flexbox-item>
        <mu-flexbox-item>
          <mu-text-field
            label="密码"
            labelFloat
            type="password"
            icon="https"
            v-model="password"/>
        </mu-flexbox-item>
      </mu-flexbox>
    </div>
    <mu-card-actions>
      <mu-raised-button label="登录" v-on:click="login" primary/>
    </mu-card-actions>
  </mu-card>
</template>

<script>
  import api from '../services/api'
  export default {
    data: function () {
      return {
        email: '',
        password: '',
        error: ''
      }
    },
    methods: {
      login: function () {
        let email = this.email
        let password = this.password
        let cookie = this.$cookie
        let loginPromise = new Promise(function (resolve, reject) {
          if (email && password) {
            resolve(true)
          } else {
            console.log(2)
            reject(false)
          }
        })
        loginPromise.then(function () {
          api.postData(api.Api.login, JSON.stringify({
            'email': email,
            'password': password
          })).then(response => {
            console.log(response)
            if (response.status === 200) {
              cookie.set(
                'user',
                {
                  token: response.data.token,
                  username: response.username
                },
                {expires: response.data.expiration.toString()})
            }
          })
        }
        ).catch(() => {
          this.error = '不能为空'
          alert('用户名或密码不能为空')
        })
      }
    }
  }
</script>

<style scoped>
  .login {
    margin: 8px;
  }
</style>



